.recipe {
  font-family: "Montserrat", sans-serif;
  border-radius: 10px;
  box-shadow: 0px 5px 20px rgb(71, 70, 70);
  margin: 20px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background: linear-gradient(-225deg, #e3fdf5 0%, #ffe6fa 100%);
  align-items: center;
  min-width: 40%;
}

.image {
  border-radius: 50%;
  width: 100px;
  height: 100px;
}

.calories {
  margin-top: 0px;
}

.caloryh4 {
  margin-bottom: 0px;
}
