.App {
  min-height: 100vh;
  background-image: linear-gradient(-60deg, #ff5858 0%, #f09819 100%);
}

.firsth1 {
  font-family: "Lobster", cursive;
  color: rgb(255, 238, 0);
  font-weight: 200;
  font-size: 6rem;
  padding: 0%;
  margin: 0%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
}

.search-button {
  background: rgb(252, 0, 0);
  border: none;
  padding: 10px 20px;
  color: white;
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
